
import Vue from 'vue'
import sessionMenagement from '../utils/StorageManagement';
import Confirm from 'vux/src/components/confirm/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Loading from 'vux/src/components/loading/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'

import { request } from '../utils/HttpRequest/request';
import {mapActions} from 'vuex'
import districts from './../assets/districts.json'

Vue.use(AlertPlugin)
export default {
directives: {
TransferDom
},
components: {
Flexbox,
FlexboxItem,
Divider,
XInput,
Group,
XButton,
Cell,
XAddress,
XHeader,
LoadingPlugin,
Loading,
Confirm
},
data () {
return {
name: '',   //收货人
tel: '',   //手机号码
area: [],   //所在区域
detailaddress: '',  //详细地址
disabled: true,
swit: false,
addressChoosed: '',
hasChoosed: false,
nameValue: function(value){
return {
valid:  /^.{1,20}$/.test(value),
msg: "验证码有误!"
}
},
telValue: function (value) {
return {
valid: value.length >= 11 && /^1[34578]\d{9}$/.test(value),
msg: "请输入11位手机号码"
}
},
areaValue: function (value) {
return {
valid: value.length === 0,
msg: "验证码有误!"
}
},
detailaddressValue: function (value) {
return {
valid: /^.{1,50}$/.test(value.trim()),
msg: "验证码有误!"
}
},
addressData: districts,
title: '所在区域',
showAddress: false,
address_One:0,
loadingShow:false,
member_id: '',
showTip: '', //提示语
showtishi: false,//控制弹窗显示隐藏
showCancel: false
}
},
methods: {
...mapActions({
getAddress: 'getAddressList',
createAddress: 'createNewAddress',
updateAddress: 'updateAddress',
deleteAddress: 'deleteAddress'
}),
chooseArea (type) {
if (type) {
this.hasChoosed = true
}
console.log(arguments)
},

checkName (name) {
if (name == '') {



this.showtishi = true;
this.showTip = '收货人不能为空'
} else {
return true;
}
return false;
},
checkTel (tel) {
if (tel.trim() == '') {



this.showtishi = true;
this.showTip = '手机号码不能为空'
} else if (tel.length != 11) {



this.showtishi = true;
this.showTip = '手机号字符必须为11位'
} else if (!(/^1[34578]\d{9}$/.test(tel.trim()))) {



this.showtishi = true;
this.showTip = '手机号字符格式不正确'
} else {
return true;
}
return false
},
checkarea(area) {
if (area.length == 0 || !this.hasChoosed) {



this.showtishi = true;
this.showTip = '请选择区域'
} else {
return true;
}
return false;
},

checkaddress(detailaddress) {
if (detailaddress == '') {



this.showtishi = true;
this.showTip = '请输入详细地址'
} else {
return true;
}
return false;
},

async saveInfo () {
console.log('点击保存');
if (this.checkName(this.name) && this.checkTel(this.tel) && this.checkarea(this.area) && this.checkaddress(this.detailaddress) && this.swit) {
this.loadingShow = true;
let res = await this.createAddress({
districtId: this.area[2] ? this.area[2] : (this.area[1] ? this.area[1] : this.area[0]),
phone: this.tel,
name: this.name,
address: this.addressChoosed + this.detailaddress
})



























console.log(res)
this.loadingShow = false;
this.$router.go(-1)
}
},

validRule () {
if (this.$refs.refname.valid == true && this.name != '' && this.$refs.reftel.valid == true && this.tel != '' && this.$refs.refdetailaddress.valid == true && this.detailaddress != '') {
this.swit = true
} else {
this.swit = false
}
},
onShadowChange (ids, names) {








this.area = ids
this.addressChoosed = names.join('')
console.log(arguments)
}
},
mounted () {
this.member_id = sessionMenagement.getMemberId()
}
}
